body {
  margin: 0;
  font-family:'Times New Roman', Times, serif;
}

.App{
  display: flex;
  justify-content: center;
  height: 100vh;
  align-items: center;
  background:linear-gradient(#3eadcf,#abe9cd);
}
.container{
  background-color: #fff;
  margin: 100px auto;
  height: 70vh;
  padding: 20px 40px 80px;
  border-radius: 8px;
}
.container h1{
  color: #3eadcf;
  text-align: center;
}
input[type="text"]{
  outline: none;
  background-color: transparent;
  color: #333 !important;
  border: none;
  width: 95%;
  border-bottom: 1px solid #fff;
  display: block;
  padding: 10px;
background-color: #f5f5f5;
  font-size: 20px;
  margin-bottom: 20px;
border-radius: 5px;
}
button{
  border: none;
  padding: 8px 20px;
  margin-bottom: 5px;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  background-color: #222;
  color: #fff;
}
.info{
  color: #333;
  font-size: 22px;
  padding: 5px;
}
